	module.exports = "<!--!\n{\n  \"title\": \"Markdown 效果演示\",\n  \"date\": \"2016-06-28\",\n  \"class\": \"post\",\n  \"tags\": [\"其它\"],\n  \"img\": {\n    \"url\": \"http://ob54s56n6.bkt.clouddn.com/markdown2.png\",\n    \"position\": [\"50%\"]\n  }\n}\n--> <p>以下效果使用默认的文本样式和代码样式。</p> <p>文本样式来自<a href=http://www.jianshu.com/ >简书</a>. 文本样式文件位于项目目录<code>src/stylesheets/md_styles</code></p> <p>代码样式为<a href=https://highlightjs.org/static/demo/ >highlights.js的样式库</a>中的&quot;Darkula&quot;样式. 代码样式文件位于项目目录<code>src/stylesheets/hljs_styles</code></p> <h2 id=->标题</h2> <h1 id=h1>h1</h1> <h2 id=h2>h2</h2> <h3 id=h3>h3</h3> <h4 id=h4>h4</h4> <h5 id=h5>h5</h5> <h6 id=h6>h6</h6> <h2 id=->区块引用</h2> <blockquote> <p>区块引用</p> <blockquote> <p>嵌套</p> </blockquote> </blockquote> <h2 id=->链接</h2> <p>支持锚链接，锚链接对象需要用HTML语法。</p> <p>为了兼容hashbang模式（例如github-page不支持HTML５模式下直接访问链接），锚链接是用js模拟的。</p> <p>如果链接的目标是自己的其它页面，可以实现无刷新跳转。</p> <p>这是一个 <a href=/ >回到主页</a>的链接</p> <p>这是一个 <a href=http://www.baidu.com/ >去百度</a>的链接</p> <p>这是一个 <a href=#bottom>到页面底端</a>的链接</p> <h2 id=->图片</h2> <p><img src=http://ob54s56n6.bkt.clouddn.com/Konachan.com%20-%20207983%20sample.jpg alt=\"\"></p> <p>也可以引用本地图片，使用相对路径即可，比如下图的Vue Logo</p> <p><img src=" + __webpack_require__(181) + " alt=\"\"></p> <p>如果你的空间有存储限制，还是尽量用图片链接比较好。</p> <h2 id=->列表</h2> <p>无序列表</p> <ul> <li>1</li> <li>2</li> <li>3</li> </ul> <p>有序列表</p> <ol> <li>1</li> <li>2</li> <li>3</li> </ol> <h2 id=->代码</h2> <p>行内代码</p> <p>使用行内<code>inline</code>代码，就像<code>this</code>这样。行内代码没有样式</p> <p>块级代码</p> <pre><code class=lang-javascript>function comparator(...rules) {\n  return function(x, y) {\n    let curRuleResult = 0\n    rules.some(function(rule) {\n      curRuleResult = rule(x, y) ? -1 : (rule(y, x) ? 1 : 0)\n      if(curRuleResult !== 0) return true\n      else return false\n    })\n    return curRuleResult\n  }\n}\n\nlet rule1 = (x, y) =&gt; (Math.abs(x) &lt; Math.abs(y))\nlet rule2 = (x, y) =&gt; (x &gt; y)\nlet result = [2, 3,-1, -3, 0, -108, 42, 10].sort(\ncomparator(rule2, rule1))\nconsole.log(result)\n</code></pre> <h2 id=->表格</h2> <table> <thead> <tr> <th>Tables</th> <th style=text-align:center>Are</th> <th style=text-align:right>Cool</th> </tr> </thead> <tbody> <tr> <td>col 3 is</td> <td style=text-align:center>right-aligned</td> <td style=text-align:right>$1600</td> </tr> <tr> <td>col 2 is</td> <td style=text-align:center>centered</td> <td style=text-align:right>$12</td> </tr> <tr> <td>zebra stripes</td> <td style=text-align:center>are neat</td> <td style=text-align:right>$1</td> </tr> </tbody> </table> <p></p><p id=bottom>你来到了页面底端</p> ";

